.submenu{
  height: calc(100% + 5.25em);
  width: calc(100% + 6.44em);
  margin-top: -2em;
  margin-left: -6.44em;
  position: relative;
  min-height: 100%;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.75em));
    gap: 0 3.5em;
  }
  &__items{
    height: 100%;
    width: 23em;
    background-color: $white;
    padding: 1.5em .25em;
  }
}

.submenu-item{
  transition: all .3s;
  &._open{
    background: #F8F8F8;
    .submenu-item__show{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    .submenu-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    padding: 1em 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    cursor: pointer;
    span{
      line-height: 120%;
      text-decoration: underline;
      text-decoration-color: rgba(148, 148, 148, 0.50);
    }
    svg{
      width: .75em;
      margin-left: 3em;
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 23em;
    padding: 2.5em 1.75em 2.5em 3.5em;
    width: calc(100% - 23em);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
  &__links{
    @include grid100gap(1em);
  }
  &__link{
    font-size: 1em;
    line-height: 119%;
    letter-spacing: -0.02em;
  }
  &__bottom{
    display: flex;
    margin-top: 2em;
  }
  &__more{
    font-weight: 600;
    color: $primary;
    &:hover{
      color: $black;
      path{
        fill: $black;
      }
    }
    span{
      font-size: 1em;
      line-height: 119%;
      letter-spacing: -0.02em;
    }
    svg{
      width: .75em;
      margin-left: .625em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.75em));
    gap: 0 3.5em;
    max-width: 57em;
  }
}

.submenu-spoiler{
  &._open{
    .submenu-spoiler__show:before{
      background-size: 0 auto, 100% auto;
    }
  }
  &__show{
    position: relative;
    cursor: pointer;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      margin-right: .75em;
      width: 1.25em;
      height: 1.25em;
      background-image: url(../img/plus.svg),url(../img/minus.svg);
      @include bgImgSize(100%);
      background-size: 100% auto, 0 auto;
    }
    span{
      font-size: 1em;
      line-height: 119%;
      letter-spacing: -0.02em;
    }
  }
  &__hidden{
    padding: .625em 1.125em;
    display: none;
    margin-top: .125em;
  }
  &__links{
    @include grid100gap(.625em);
    color: #7C7B7B;
    a{
      font-size: 1em;
      line-height: 119%;
      letter-spacing: -0.02e
    }
  }
}



@media screen and (max-width: 992px){
  .submenu{
    width: 100%;
    height: unset;
    margin: 0;
    &__items{
      width: 100%;
      padding: 0;
    }
  }

  .submenu-item{
    border-bottom: 1px solid #F0F0F0;
    margin-bottom: .25em;
    &._open{
      border-color: transparent;
      .submenu-item__show{
        svg{
          transform: rotate(-90deg);
        }
      }
    }
    &__show{
      padding: 1em;
      justify-content: unset;
      svg{
        order: -1;
        margin: 0 .75em 0 0;
      }
    }
    &__hidden{
      display: none;
      position: static;
      padding: .5em .5em 1.25em;
      width: 100%;
      transition: unset;
      opacity: 1;
      pointer-events: unset;
    }
    &__links{
      gap: .75em;
      padding-left: 2em;
    }
    &__link{
      //font-size: .9375em;
      //line-height: 127%;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__bottom{
      padding-left: 2em;
    }
  }
}


