.press-center{
  &__link{

  }
  &__tabs{
    display: inline-flex;
    border-bottom: 2px solid #E6E6E6;
  }
  &__tab{
    padding: 0 .5em 1.3125em;
    margin-right: 1em;
    font-weight: 500;
    color: #5A5A5A;
    border-bottom: .1875em solid transparent;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      color: $black;
    }
    &._active{
      pointer-events: none;
      border-color: $primary;
      color: $primary;
    }
    span{
      font-size: 1.4375em;
      line-height: 147%;
    }
    &:last-child{
      margin: 0;
    }
  }
}

.blog-slider{
  @media screen and (min-width: 993px){
    .swiper-wrapper{
      @include grid100gap(1em);
      transform: unset !important;
    }
    .swiper-slide:last-child{
      .blog-card__content{
        border-bottom: none;
      }
    }
  }
}

.blog-card{
  display: grid;
  grid-template-columns: 20em calc(100% - 20em);
  &__img{
    @include imgCover(50%);
    min-height: 100%;
  }
  &__content{
    padding: 1.25em 2em;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  }
  &__props,
  &__prop{
    display: flex;
    align-items: center;
  }
  &__prop{
    color: #A6A6A6;
    font-size: 1em;
    line-height: 140%;
    &:not(:first-child){
      &:before{
        content: '';
        width: 1.0625em;
        height: 1.0625em;
        background-image: url(../img/dot.svg);
        @include bgImgSize(.25em);
        margin: 0 .125em;
        flex-shrink: 0;
      }
    }
  }
  &__bottom{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 30em calc(100% - 33.125em);
    gap: 1.25em 3.125em;
    align-items: center;
  }
  &__title{
    font-size: 1.875em;
    font-weight: 600;
    line-height: 120%;
  }
  &__desc{
    color: $text-light-grey;
    font-size: 1em;
    line-height: 140%;
    font-family: $font3;
    @include truncate(3);
  }
}


@media screen and (max-width: 992px){
  .press-center{
    overflow: hidden;
  }

  .blog-slider{
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      width: 20.75em;
      padding-right: .75em;
    }
  }

  .blog-card{
    grid-template-columns: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    height: 100%;
    &__content{
      padding: 1.25em 1em;
      border-bottom: none;
    }
    &__bottom{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .press-center{
    &__inner{
      position: relative;
      padding-bottom: 9em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    &__link{
      width: 100%;
      height: 4em;
      justify-content: center;
    }
    &__tabs{
      font-size: .875em;
    }
  }
}
