.header{
  &._open-search,
  &._show-dropdown{
    padding-right: var(--scrollWidth);
    background: $white;
    color: $black;
    .header__logo{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .nav-item__show:after{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.46875 8.59375H7.03125V10.1562H5.46875V8.59375ZM7.03125 10.1562H8.59375V11.7188H7.03125V10.1562ZM8.59375 11.7188H10.1562V13.2812H8.59375V11.7188ZM10.1562 13.2812H11.7188V14.8438H10.1562V13.2812ZM11.7188 14.8438H13.2812V16.4062H11.7188V14.8438ZM13.2812 13.2812H14.8438V14.8438H13.2812V13.2812ZM14.8438 11.7188H16.4062V13.2812H14.8438V11.7188ZM16.4062 10.1562H17.9688V11.7188H16.4062V10.1562ZM17.9688 8.59375H19.5312V10.1562H17.9688V8.59375Z' fill='%23FF0024'/%3E%3C/svg%3E");
    }
  }
}

.nav-item{
  &._open{
    .nav-item__show:before{
      opacity: 1;
    }
    .nav-item__show:after{
      transform: rotate(-180deg);
    }
    .nav-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .nav-dropdown__bg{
      display: block;
    }
  }
}

.header-search__bg,
.nav-dropdown__bg{
  z-index: 99;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  height: calc(100 * var(--vh) - 6.25em);
  background: rgba(0, 0, 0, 0.51);
  display: none;
}

.nav-dropdown{
  //transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 6.25em;
  left: 0;
  width: 100%;
  padding: 2em 3.5em 3.25em 6.44em;
  background-color: $white;
  z-index: 100;
  display: grid;
  grid-template-columns: calc(100% - 20em) 17.8125em;
  justify-content: space-between;
  font-family: $font2;
  min-height: 25em;
  &__links{
    @include grid100gap(1em);
    a{
      font-size: 1em;
      line-height: 119%;
      letter-spacing: -0.02em;
    }
  }
  &__col{}
  &__back{
    display: none;
  }
}

.menu-card{
  font-family: $font4;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    @include grid100gap(.375em);
    margin-top: .75em;
  }
  &__title{
    font-size: 1em;
    font-weight: 700;
    letter-spacing: -.02em;
  }
  &__desc{
    font-size: .875em;
    line-height: 114%;
    letter-spacing: -.02em;
    color: #535457;
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__link{
    height: 2.125em;
    padding: 0 1em;
    font-family: $font4;
    font-weight: 700;
    span{
      font-size: .75em;
      letter-spacing: -.02em;
    }
  }
}

.nav-dropdown2{
  background-color: #F8F8F8;
}


@media screen and (max-width: 992px){
  .nav-dropdown{
    position: fixed;
    top: unset;
    bottom: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 4em);
    background-color: $white;
    //opacity: 1;
    //pointer-events: unset;
    padding: 1em;
    display: block;
    overflow-y: auto;
    &__back{
      display: flex;
      align-items: center;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: 1.19em;
      color: $black;
      span{
        font-size: 1.25em;
        line-height: 95%;
        letter-spacing: -.02em;
      }
      img{
        width: .75em;
        margin-right: .56em;
      }
    }
    &__col{
      min-height: unset !important;
    }
    &__col2{
      display: none;
    }
  }
}


