.media{
  padding: 3.75em 0;
  .h2{
    line-height: 112%;
    span{
      color: rgba(57, 57, 57, 0.40);
    }
  }
  &__items{
    margin-top: 4.375em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
    gap: 1.75em;
  }
}

.media-item{
  padding: .1875em;
  border: 1px solid #E2E2E2;
  display: grid;
  grid-template-columns: 6.55em calc(100% - 6.55em);
  align-items: center;
  position: relative;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
  &__img{
    @include imgCover(100%);
  }
  &__content{
    padding: 1em 3em 1em 2.31em;
  }
  &__title{
    color: #959595;
    font-size: 1.125em;
    line-height: 130%;
    margin-bottom: .1em;
  }
  &__desc{
    font-family: $font3;
    font-size: 1.5625em;
    line-height: 130%;
  }
  &__arrow{
    position: absolute;
    right: .625em;
    bottom: .625em;
    background: #F6F6F6;
    width: 2.75em;
    height: 2.75em;
    @include center;
    transition: all .3s;
    &:hover{
      background-color: rgba($light-red,.5);
    }
    img{
      width: 1.25em;
    }
  }
}

.main{
  overflow: hidden;
  background-image: url(../img/main.webp);
  background-color: $black;
  color: $white;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: flex-end;
  &__inner{
    width: 100%;
  }
}

.main-slider{
  width: 100%;
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper-counter{
    font-size: 1.125em;
    line-height: 150%;
  }
  .swiper-pagination{
    position: static;
    width: auto;
    margin: 0 1.5em 0 0;
    display: flex;
    align-items: center;
    &-bullet{
      border-radius: 0;
      width: 2.53em;
      height: 0.25em;
      margin: 0 .31em 0 0 !important;
      background-color: $white;
      opacity: .3;
      position: relative;
      &:before{
        content: '';
        @include centerAbsolute;
        height: 300%;
        width: 100%;
      }
      &-active{
        opacity: 1;
      }
    }
  }
  &__bottom{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding-bottom: 8.56em;
  }
}

.main-card{
  padding: 7em 0 15.25em;
  min-height: 56.25em;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  &__content{
    max-width: 61em;
  }
  &__title{
    font-size: 5.625em;
    font-weight: 600;
  }
  &__desc{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .media{
    padding: 5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
      margin-top: 2.5em;
    }
  }

  .media-item{
    grid-template-columns: 4.82em calc(100% - 4.82em);
    &__content{
      padding: .5em 3em .5em 1.25em;
    }
    &__title{
      font-size: 1em;
      margin-bottom: .125em;
    }
    &__desc{
      font-size: 1.25em;
    }
    &__arrow{
      right: 0;
      bottom: 0;
    }
  }

  .main-slider{
    &__bottom{
      padding-bottom: 3.69em;
    }
  }

  .main-card{
    padding: 7em 0 10.375em;
    min-height: 36.875em;
    &__content{
      max-width: 28em;
    }
    &__title{
      font-size: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .media{
    &__items{
      grid-template-columns: 100%
    }
  }

  .main{
    background-image: url(../img/main2.webp);
  }

  .main-card{
    min-height: 46.875em;
    &__nav{
      width: 100%;
    }
    &__link{
      width: 100%;
    }
  }
}
