.solution-card{
  display: grid;
  grid-template-columns: 22.5em calc(100% - 22.5em - 8.75em);
  gap: 3.125em 8.75em;
  height: 100%;
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-size: 3.125em;
    line-height: 120%;
    font-weight: 500;
  }
  &__props{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    align-items: flex-start;
  }
  &__prop{
    @include grid100gap(1em);
    &-num{
      font-size: 2.875em;
      font-weight: 500;
      line-height: 130%;
    }
    &-text{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
  &__subtitle{
    font-size: 1.75em;
    line-height: 129%;
  }
  &__list{
    margin-top: 2.1875em;
    @include grid100gap(1em);
  }
  &__item{
    display: grid;
    grid-template-columns: 2.06em calc(100% - 2.06em);
    &-num{
      color: #B3B3B3;
    }
    &-num,
    &-text{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
  &__nav{
    margin-top: auto;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    padding-top: 1.5em;
  }
}

.awards{
  &__desc{
    font-size: 2em;
    line-height: 150%;
    margin-top: .9em;
  }
  &__grid{
    margin-top: 5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15em / 4));
    gap: 5em;
    align-items: flex-start;
  }
}

.award-item{
  padding-top: 2em;
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  @include grid100gap(1.5em);
  position: relative;
  &__num{
    background: linear-gradient(90deg, #FF0024 0%, #FF611B 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 4.5625em;
    font-weight: 500;
    line-height: 110%;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 130%;
    color: $text-light-grey;
  }
}

.tooltip-target{
  border-bottom: 1px dotted $text-light-grey;
  transition: all .3s;
  display: inline-block;
  cursor: pointer;
  line-height: inherit;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
}


@media screen and (max-width: 992px){
  .solution-card{
    grid-template-columns: 100%;
    &__title{
      font-size: 2em;
      line-height: 130%;
    }
    &__props{
      display: none;
    }
    &__subtitle{
      font-size: 1.25em;
      line-height: 130%;
      font-weight: 400;
    }
    &__list{
      margin-top: 2em;
      gap: .75em;
    }
    &__item{
      &:nth-child(4)~.solution-card__item{
        display: none;
      }
    }
  }

  .awards{
    &__desc{
      font-size: 1.375em;
      line-height: 130%;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.5em));
      gap: 2.69em 3em;
    }
  }

  .award-item{
    padding-top: 1.5em;
    &__num{
      font-size: 5em;
    }
    &__desc{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .solution-card{
    &__nav{
      grid-template-columns: 100%;
    }
  }

  .awards{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
