.footer{
  padding: 2.5em 0 1.5em;
  background: #141414;
  color: $white;
  &__logo{
    display: block;
    width: 3.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - 12.5em / 6));
  gap: 2.5em;
  grid-auto-flow: dense;
}

.footer-nav{
  &__items{
    color: #808080;
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    a{
      font-size: 1.375em;
      line-height: 130%;
    }
  }
  @media screen and (min-width: 993px){
    &:nth-child(4){
      grid-column: 2/3;
      grid-row: 1/2;
    }
    &:nth-child(5){
      grid-column: 4/5;
      grid-row: 1/2;
    }
  }
}

.footer-middle{
  margin-top: 3.25em;
  padding-top: 3em;
  padding-left: .75em;
  border-top: 1px solid rgba(255, 255, 255, 0.10);
  &__link{
    margin-left: 2.81em;
    border-radius: 6.25em;
    background: #232323;
    padding: 0 1.5em;
    height: 2.75em;
    @include center;
    color: $white;
    &:hover{
      color: $white;
      background-color: $substrate-grey;
    }
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
  .socials{
    margin-left: auto;
  }
}

.footer-bottom{
  margin-top: 2.5em;
  &__co{
    color: #D2D2D2;
  }
  .lang{
    margin-left: 2em;
  }
  &__col2{
    gap: .75em 1.25em;
  }
  &__link{
    font-size: .875em;
    line-height: 130%;
    text-decoration: underline;
    text-decoration-skip-ink: auto;
    color: #808080;
  }
}

.lang{
  display: flex;
  align-items: center;
  gap: .75em;
  &__item{
    font-family: $font2;
    font-size: 1em;
    color: #808080;
    line-height: 150%;
    border-bottom: 1px solid transparent;
    &._active{
      color: $primary;
      border-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 2.5em;
  }

  .footer-top{
    display: block;
    column-count: 3;
    column-gap: 1.25em;
  }

  .footer-nav{
    display: inline-block;
    width: 100%;
    margin-bottom: 1.5em;
  }

  .footer-middle{
    padding-top: 1.875em;
    margin-top: 1.875em;
  }

  .footer-bottom{
    margin-top: 1.875em;
    flex-direction: column;
    &__col2{
      width: 100%;
      margin-top: 1.875em;
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      padding-bottom: 4.375em;
      position: relative;
    }
    &__logo{
      width: 6.25em;
      padding: 1.5em 1.2em;
    }
  }

  .footer-top{
    column-count: 2;
  }

  .footer-nav{
    word-break: break-word;
    &:nth-child(6){
      padding-bottom: 8em;
    }
    &__items{
      a{
        font-size: 1.25em;
      }
    }
  }

  .footer-middle{
    flex-direction: column;
    padding-left: 0;
    border: none;
    &__link{
      margin: 1.875em 0 0;
    }
    .socials{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      justify-content: center;
    }
  }

  .footer-bottom{
    &__col2{
      flex-direction: column;
      align-items: flex-start;
    }
  }
}
