.products{
  overflow: hidden;
  .h2{
    max-width: 16em;
  }
  &__nav{
    display: flex;
    margin-top: 4.375em;
  }
}

.products-slider{
  margin-top: 4.375em;
  .swiper-slide{
    width: 22.5em;
  }
  .swiper-nav{
    margin-bottom: 4.375em;
    position: absolute;
    right: 0;
    bottom: 100%;
  }
}

.product-card{
  padding: 4.375em 2em;
  position: relative;
  transition: all .3s;
  height: 100%;
  &:hover{
    color: $white;
    .product-card__num{
      color: $white;
    }
    .product-card__list li{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='23' height='23' viewBox='0 0 23 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='3' height='3' transform='translate(10 10)' fill='%23fff'/%3E%3C/svg%3E");
    }
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/product-card-bg.webp);
    background-size: cover;
    background-position: center;
    z-index: -1;
    transition: all .3s;
    opacity: 0;
  }
  &__num{
    color: #B3B3B3;
    font-size: 6.125em;
    line-height: 130%;
    transition: all .3s;
  }
  &__content{
    margin-top: 4.375em;
    @include grid100gap(2.5em);
  }
  &__title{
    font-size: 2.25em;
    font-weight: 600;
    line-height: 130%;
  }
  &__list{
    @include grid100gap(1em);
    li{
      font-size: 1.125em;
      line-height: 130%;
      position: relative;
      padding-left: 1.72em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='23' height='23' viewBox='0 0 23 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='3' height='3' transform='translate(10 10)' fill='%231A1A1A'/%3E%3C/svg%3E");
      background-size: 1.27em auto;
      background-repeat: no-repeat;
      background-position: 0 0;
      transition: all .3s;
    }
  }
}

.solutions{
  &__grid{
    display: grid;
    grid-template-columns: 14.875em calc(100% - 15.875em);
    gap: 2em 1em;
    margin-top: 3.5em;
  }
  &__col2{
    padding: 3.125em;
    color: $white;
    background-image: url(../img/solutions-bg.webp);
    background-size: cover;
    background-position: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/blur.svg);
      background-size: 39.875em auto;
      background-repeat: no-repeat;
      background-position: left 0 bottom 0;
    }
  }
  .tab-content{
    height: 100%;
  }
}


@media screen and (max-width: 992px){
  .products{
    &__nav{
      justify-content: center;
      margin-top: 1.5em;
    }
  }

  .products-slider{
    margin-top: 1.5em;
    .swiper-slide{
      width: 20em;
    }
    .swiper-nav{
      display: none;
    }
  }

  .product-card{
    padding: 4.625em 1.5em;
  }

  .solutions{
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2em;
    }
    &__col2{
      padding: 1.5em;
      &:before{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .solutions{
    &__col2{
      background-image: url(../img/solutions-bg2.webp);
    }
  }
}
