.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 69.25em;
        max-width: calc(100% - .625em);
        position: relative;
        z-index: 3;
        padding: 2.25em;
        border-radius: 1.5em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__title{
        font-size: 2.3125em;
        font-weight: 600;
        line-height: 130%;
    }
}

.close-btn{
    width: 2.5em;
    @include center;
    margin-left: 1em;
    flex-shrink: 0;
    img{
        width: 100%;
    }
}

.sites{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 8em / 3));
    gap: 5em 4em;
}

.site-item{
    &__title{
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 500;
    }
    &__desc{
        margin-top: .5em;
        color: $text-light-grey;
        line-height: 120%;
    }
    &__nav{
        display: flex;
        margin-top: 1em;
    }
    &__link{
        font-size: 1.125em;
        line-height: 111%;
        color: $primary;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 1.5em 1.25em;
        }
        &:after{
            display: none;
        }
        &__title{
            font-size: 2em;
        }
    }

    .sites{
        margin-top: 2.75em;
        grid-template-columns: repeat(2,calc(50% - 1.25em));
        gap: 2.5em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .sites{
        grid-template-columns: 100%;
    }
}
