.reviews{
  overflow: hidden;
}

.reviews-slider{
  margin-top: 2.875em;
  .swiper-container{
    width: calc(100% + 1.25em);
  }
  .swiper-slide{
    width: 31.125em;
    padding-right: 1.25em;
  }
  .swiper-nav{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 3em;
  }
}

.review-card{
  height: 100%;
  padding: 2.5em 1.875em 4em;
  background: #F7F7F7;
  &__top{
    @include grid100gap(.5625em);
    margin-bottom: 2.94em;
  }
  &__name{
    font-size: 1.75em;
    line-height: 114%;
    font-weight: 500;
    letter-spacing: -.01em;
  }
  &__prof{
    font-size: .875em;
    line-height: 143%;
    color: #9C9898;
    letter-spacing: -.01em;
  }
  &__text{
    font-size: 1.125em;
    line-height: 133%;
    letter-spacing: -.01em;
    @include grid100gap(1em);
    p{
      line-height: inherit;
      letter-spacing: inherit;
    }
  }
}

.companies{
  overflow: hidden;
  &__link{
    height: 4em;
  }
}

.companies-slider{
  .swiper-container{
    width: calc(100% + 3em);
  }
  .swiper-slide{
    width: 25.5em;
    padding-right: 3em;
  }
}

.company-card{
  background-color: $background-grey;
  height: 100%;
  &__img{
    @include imgCover(72.2%)
  }
  &__content{
    padding: 1.25em 1em;
    @include grid100gap(.75em);
  }
  &__type{
    color: #A6A6A6;
    font-size: 1em;
    line-height: 140%;
    font-family: $font3;
  }
  &__desc{
    color: $text-light-grey;
    font-size: 1.125em;
    line-height: 150%;
    font-family: $font3;
    @include truncate(3);
  }
}


@media screen and (max-width: 992px){
  .reviews-slider{
    margin-top: 2.5em;
    .swiper-slide{
      width: 20em;
    }
    .swiper-nav{
      display: none;
    }
  }

  .review-card{
    padding: 2em 1.25em;
    &__top{
      gap: .25em;
      margin-bottom: 1.5em;
    }
    &__name{
      font-size: 1.5em;
      line-height: 117%;
    }
    &__text{
      font-size: 1.0625em;
      line-height: 141%;
    }
  }

  .companies-slider{
    .swiper-container{
      width: calc(100% + 1em);
    }
    .swiper-slide{
      width: 21em;
      padding-right: 1em;
    }
  }

}


@media screen and (max-width: 576px){
  .companies{
    &__inner{
      position: relative;
      padding-bottom: 6.5em;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }
}
