.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  color: $white;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6.25em;
    position: relative;
    margin-right: 2.625em;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.header-top{
  padding: 0 3.125em 0 1.25em;
  height: 6.25em;
}

.header-action{
  gap: 3em;
  &__btn{
    padding: 0 1.5em;
    height: 3em;
  }
}

.header-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__phone,
  &__mail{
    transition: opacity .3s;
    font-size: .875em;
    line-height: 150%;
    font-family: $font2;
    &:hover{
      opacity: .8;
    }
  }
  &__phone{
    &:hover{
      color: inherit;
    }
  }
  &__mail{
    color: $primary;
    &:hover{
      color: $primary;
    }
  }
}



.nav{
  display: flex;
  align-items: center;
  gap: 1.5em;
  &__link{

  }
}

.nav-item__show,
.nav__link{
  font-weight: 500;
  font-family: $font2;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  height: 5.875em;
  position: relative;
  transition: opacity .3s;
  &:hover{
    color: inherit;
    opacity: .9;
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: .1875em;
    background-color: $primary;
    opacity: 0;
    transition: all .3s;
  }
  span{
    font-size: 1.125em;
    line-height: 150%;
  }
  &:hover{
    color: inherit;
    &:before{
      opacity: 1;
    }
  }
}

.nav-item{
  &__show{
    cursor: pointer;
    &:after{
      content: '';
      width: 1.5625em;
      height: 1.5625em;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.46875 8.59375H7.03125V10.1562H5.46875V8.59375ZM7.03125 10.1562H8.59375V11.7188H7.03125V10.1562ZM8.59375 11.7188H10.1562V13.2812H8.59375V11.7188ZM10.1562 13.2812H11.7188V14.8438H10.1562V13.2812ZM11.7188 14.8438H13.2812V16.4062H11.7188V14.8438ZM13.2812 13.2812H14.8438V14.8438H13.2812V13.2812ZM14.8438 11.7188H16.4062V13.2812H14.8438V11.7188ZM16.4062 10.1562H17.9688V11.7188H16.4062V10.1562ZM17.9688 8.59375H19.5312V10.1562H17.9688V8.59375Z' fill='white'/%3E%3C/svg%3E");
      @include bgImgSize(100%);
      margin-left: .25em;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 4em;
      margin-right: .75em;
      position: relative;
      z-index: 11;
    }
    &._open{
      background: $white;
      color: $black;
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger,
      .header__logo{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .header-search__btn path{
        fill: #CCC;
      }
    }
  }

  .header-top{
    padding: 0 1.19em 0 0;
    height: 4em;
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    padding-top: 4em;
    background-color: $white;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    &__scroll{
      height: 100%;
      overflow-y: auto;
      padding: 1em;
    }
  }

  .header-action{
    flex-grow: 1;
    justify-content: flex-end;
    gap: .81em;
    position: relative;
    z-index: 11;
    &__btn,
    .lang{
      display: none;
    }
  }

  .header-contacts{
    margin-right: auto;
    &__mail{
      display: none;
    }
  }

  .header-search{
    &__btn{
      width: 2em;
      height: 2em;
      svg{
        width: 1.25em;
      }
      path{
        fill: $white;
      }
    }
  }

  .burger{
    display: flex;
    width: 2em;
    height: 2em;
    position: relative;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }

  .nav{
    display: block;
  }

  .nav-item__show,
  .nav__link{
    justify-content: space-between;
    height: 2.5625em;
    font-weight: 600;
    &:before{
      display: none;
    }
    span{
      font-size: 1.25em;
      letter-spacing: -.02em;
    }
  }

  .nav-item{
    &__show{
      padding-right: 2em;
      background-image: url(../img/arrow-right3.svg);
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 1.5625em auto;
      &:after{
        display: none;
      }
    }
  }
}


