@import "_header";
@import "_header2";
@import "_submenu";
@import "_footer";

.header{
  &._open-search{
    .header-search__hidden{
      opacity: 1;
      pointer-events: unset;
    }
    .header-search__bg{
      display: block;
    }
  }
}

.header-search{
  position: relative;
  z-index: 2;
  &__btn{
    @include center;
    width: 1.75em;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -.75em;
    width: 28.25em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
}

.m-search{
  position: relative;
  &__close{
    width: 1.75em;
    height: 1.75em;
    @include center;
    position: absolute;
    top: 50%;
    right: .75em;
    transform: translateY(-50%);
    img{
      width: .9375em;
    }
  }
  &__icon{
    width: 1.75em;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .875em;
  }
  input{
    padding-left: 3.25em;
  }
}


@media screen and (max-width: 992px){
  .header-search{
    position: static;
    &__hidden{
      background-color: $white;
      position: fixed;
      top: 0;
      transform: unset;
      right: 0;
      width: 100%;
      height: 4em;
      padding: .625em .75em;
    }
    &__bg{
      height: calc(100 * var(--vh) - 4em);
    }
  }

  .m-search{
    input{
      height: 2.75em;
    }
  }
}


