.tooltip-parent{
  &._show{
    .m-tooltip{
      opacity: 1;
      pointer-events: unset;
    }
    .tooltip-target{
      color: $primary;
      border-color: $primary;
    }
  }
}

.m-tooltip{
  position: absolute;
  left: -10.25em;
  bottom: 100%;
  margin-bottom: -7em;
  background: rgba(0, 0, 0, 0.95);
  color: #F7F7F7;
  width: 36.8125em;
  padding: 1.5em;
  z-index: 2;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &:before{
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 1.1875em;
    height: 1.1875em;
    background: inherit;
    transform: translate(-50%,50%) rotate(-45deg);
  }
  &__text{
    font-size: 1.0625em;
    line-height: 135%;
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
}

.partners{
  overflow: hidden;
  position: relative;
  &__top{
    @include centerAbsolute;
    padding: 1.25em;
    min-width: 50em;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.00) 0.42%, #FFF 5%, #FFF 50%, #FFF 95%, rgba(255, 255, 255, 0.00) 100%);
    text-align: center;
    @include grid100gap(.25em);
    z-index: 2;
  }
  &__bottom{
    padding: 2em 0;
    display: flex;
    align-items: center;
    &:hover .partners__items{
      animation-play-state: paused;
    }
  }
  &__items{
    display: flex;
    align-items: center;
    animation: 40s slides infinite linear;
  }
}

.partner-item{
  height: 5em;
  margin-right: 9em;
  flex-shrink: 0;
  img{
    height: 100%;
    display: block;
  }
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}


@media screen and (max-width: 992px){
  .m-tooltip{
    left: 0;
    width: 25em;
    &:before{
      left: 6em;
    }
    &__text{
      font-size: .875em;
    }
  }

  .partners{
    padding-top: 3.5em;
    position: relative;
    &__top{
      position: static;
      transform: unset;
      min-width: unset;
    }
    &__bottom{
      padding: 0;
      margin-top: 1.25em;
    }
    &__items{
      animation-duration: 30s;
    }
  }

  .partner-item{
    height: 3.5em;
    margin-right: .69em;
  }
}


@media screen and (max-width: 576px){
  .m-tooltip{
    width: 100%;
  }
}
