.solutions-tabs{
  min-height: 100%;
  height: 45.3125em;
  overflow: hidden;
  position: relative;
  .swiper-container{
    height: 100%;
  }
  .swiper-slide{
    height: 7.4375em;
  }
  .swiper-button{
    position: absolute;
    left: 0;
    right: unset;
    margin: 0;
    width: 100%;
    height: 3.75em;
    border: none;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: $white;
      filter: blur(20px);
    }
    &:hover{
      background: unset;
    }
    img{
      width: 2em;
      position: relative;
      z-index: 2;
    }
    &-prev{
      top: 0;
    }
    &-next{
      top: unset;
      bottom: 0;
    }
  }
  .swiper-button-disabled{
    opacity: 0;
  }
}

.solutions-tab{
  border-top: 1px solid rgba(0, 0, 0, 0.10);
  padding: 1em .75em;
  position: relative;
  transition: all .3s;
  height: 100%;
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &._active{
    color: $white;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/tab-bg.webp);
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: all .3s;
    z-index: -1;
  }
  &__num{
    opacity: 0.6;
    line-height: 130%;
    margin-bottom: .625em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .solutions-tabs{
    height: unset;
    overflow: unset;
    .swiper-container{
      height: unset;
    }
    .swiper-slide{
      height: unset;
      width: 9.125em;
      &:last-child{
        .solutions-tab{
          border-right: none;
        }
      }
    }
    .swiper-button{
      display: none;
    }
  }

  .solutions-tab{
    border-top: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.10);
    border-right: 1px solid rgba(0, 0, 0, 0.10);
    padding: 1em .75em;
    &:hover,
    &._active{
      color: $black;
    }
    &:before{
      background: unset;
      border-bottom: 2px solid $primary;
    }
    &__num{
      font-size: .8125em;
      line-height: 123%;
    }
    &__title{
      font-size: .875em;
      line-height: 115%;
    }
  }
}


