.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 3.375em;
    font-weight: 600;
    font-family: $font2;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
    }
    svg,
    img{
        width: 1.5em;
        margin: 0 .75em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-grad{
    background: linear-gradient(90deg, #FF0024 0%, #FF611B 100%);
    color: $white;
    &:hover{
        color: $white;
        opacity: .9;
    }
}

.m-btn-red{
    background: $primary;
    color: $white;
    &:hover{
        color: $white;
        background: darken($primary,10%);
    }
}

.m-btn-gray-outline{
    border: 2px solid #EBEBEB;
    color: $black;
    &:hover{
        color: $black;
        background-color: rgba($light-red,.5);
        border-color: rgba($light-red,.5);
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        color: $black;
        background-color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        color: $black;
        background-color: rgba($light-red,.5);
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    color: $primary;
    font-family: $font2;
    font-weight: 600;
    text-transform: uppercase;
    &:hover{
        color: $primary;
        opacity: .8;
    }
    span{
        font-size: 1.125em;
        line-height: 130%;
    }
    img{
        width: 1.5em;
        margin-left: .75em;
    }
}
