.join-us{
  padding: 12.5em 0;
  color: $white;
  background-color: $black;
  background-image: url(../img/join-us.webp);
  background-size: cover;
  background-position: center;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    text-align: center;
    z-index: 2;
  }
  &__link{
    margin-top: 3.5em;
  }
  @media screen and (min-width: 993px){
    .h2{
      font-size: 4.25em;
    }
  }
}

.m-section{
  padding: 5em 0;
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
  }
}

.last-videos{
  overflow: hidden;
}

.video-card{
  display: block;
  position: relative;
  color: $white;
  &:hover{
    color: $white;
  }
  &__img{
    @include imgCover(48.61%);
    &:before{
      content: '';
      @include centerAbsolute;
      width: 4.5em;
      height: 4.5em;
      background-image: url(../img/play.svg);
      @include bgImgSize(100%);
      z-index: 2;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 3.125em;
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    margin: 0 1em 0 0;
    &-next{
      margin-right: 0;
    }
  }
}

.swiper-button{
  border: 2px solid #EBEBEB;
  width: 4em;
  height: 4em;
  @include center;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    border-color: $primary;
    path{
      stroke: $white;
    }
  }
  svg{
    width: 1.5em;
  }
}

.videos-slider{
  &__bottom{
    position: absolute;
    right: 0;
    bottom: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 5.19em;
  }
  .swiper-nav{
    margin-left: 1.5em;
  }
  .swiper-counter{
    font-family: $font3;
    position: static;
    width: auto;
    font-size: 1.125em;
    line-height: 150%;
  }
  .swiper-container{
    width: 100%;
  }
  .swiper-slide{
    padding: 0;
    position: relative;
    z-index: 1;
    transform: scale(.8);
    transition: all .3s;
    &-next,
    &-prev{
      transform: scale(.9);
    }
    &-active{
      transform: scale(1);
    }
  }
}


@media screen and (max-width: 992px){
  .join-us{
    padding: 6.25em 0;
    &__link{
      margin-top: 5.3125em;
    }
  }

  .videos-slider{
    &__bottom{
      position: static;
      margin: 5em 0 0;
    }
  }

  .video-card{
    &__content{
      padding: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .join-us{
    background-image: url(../img/join-us2.webp);
    &__link{
      width: 100%;
    }
  }

  .video-card{
    &__img{
      padding-bottom: 85.4%;
    }
  }
}
